import {Link} from 'react-router-dom';
function Navbar() {
  return (
    <div>
    <nav>
      <ul>
        <li><Link to = "/" className = "li">Home</Link></li>
        <li><Link to = "/Signup" className = "li">Signup</Link></li>
        <li><Link to = "/Login" className = "li">Login</Link></li>
      </ul>
    </nav>
    </div>
    
  );
}
export default Navbar;