import bg from './bg.JPG';
import { Link } from 'react-router-dom';

function Home() {
  return (
    <div className="Home">

      <div className="image">
        <img src={bg} alt = "text"/>
      </div>

      <h1>
        Welcome To FacePage
      </h1>
      <h3>
        Connect With Your Friends In just one Click...
    </h3>

      <Link to="/Signup">
        <button className="SignUpBtn">
          Signup
     </button>
      </Link>
      <Link to="/Login">
        <button className="LoginBtn">
          Login
   </button>
      </Link>
      <div className="footer">
        <h2>
          Made With Love By ECCians ❤️
      </h2>
      </div>

    </div>
  );
}

export default Home;
